.button {
  height: 40px;
  font-family: inherit;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  text-transform: uppercase;

  border-radius: 5px;
}
.event__link {
  display: block;
  width: 145px;
  background-color: var(--modal-accent-color);
  color: #ffffff;
  border: transparent;
  text-align: center;
  text-decoration: none;
  padding: 10px 0;
  margin-left: auto;
  margin-right: auto;
  transition: background-color var(--transition-delay) var(--transition-cube-function),
    transform var(--transition-delay) var(--transition-cube-function);
}
.event__link:hover {
  background-color: var(--accent-color);
  transform: scale(1.25);
}
