.modal__btn-more {
  text-align: center;
  padding: 10px 0;
  display: block;
  margin-right: auto;
  margin-left: auto;
  margin-top: 5px;
  min-width: 230px;
  background-color: transparent;
  color: var(--modal-accent-color);
  border: solid 1px var(--modal-accent-color);
  transition: border-color var(--transition-delay) var(--transition-cube-function),
    transform var(--transition-delay) var(--transition-cube-function),
    color var(--transition-delay) var(--transition-cube-function);

  @media screen and (min-width: 768px) {
    width: 255px;
    border-color: var(--accent-color);
  }
  @media screen and (min-width: 1280px) {
    border-color: var(--modal-accent-color);
  }
}
.modal__btn-more:hover {
  transform: scale(1.25);
  color: var(--accent-color);
  border-color: var(--accent-color);
  @media screen and (min-width: 768px) and (max-width: 1279.8px) {
    color: var(--accent-color);
    border-color: var(--modal-accent-color);
  }
}
